* {
  box-sizing: border-box;
}

h2, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  color: #fff;
  height: 100%;
  background-color: #1e5a1e;
  margin: 0;
  padding: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.5;
  position: relative;
}

.btn-back {
  color: currentColor;
  gap: 10px;
  margin-bottom: 16px;
  margin-right: auto;
  text-decoration: none;
  transition: transform .25s;
  display: flex;
}

.btn-back:hover, .btn-back:focus {
  transform: translateX(-10px);
}

.btn-back span {
  width: 40px;
  height: 40px;
  background-color: #252121;
  background-image: url("icon-back.159d58f4.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  border: 2px solid #fff;
  border-radius: 50%;
  display: flex;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px 20px;
}

.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list__item {
  align-items: center;
  gap: 20px;
  display: flex;
}

.country-list__item:not(:last-child) {
  margin-bottom: 10px;
}

.country-info img {
  margin-bottom: 16px;
}

.country-info p {
  margin-bottom: 10px;
}

.country-info__title {
  margin-bottom: 16px;
}

body {
  position: relative;
}

.container {
  height: 100%;
  flex-direction: column;
  display: flex;
}

#country-search-box {
  font: inherit;
  width: 100%;
  margin-bottom: 16px;
  padding: 8px 20px;
}

.country-info-box {
  height: 100%;
  flex-grow: 1;
  margin-bottom: 16px;
  position: relative;
}

.country-info-box.grid {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 16px;
  display: grid;
}

.card, .country-card {
  text-align: center;
  background-color: #252121;
  border: 2px solid #fff;
  border-radius: 10px;
  padding: 16px;
  transition: opacity .25s, visibility 2s;
  display: flex;
}

.card {
  cursor: pointer;
  flex-direction: column;
  align-items: center;
}

.card * {
  pointer-events: none;
}

.card img {
  height: 50px;
}

.card p {
  flex-grow: 1;
  align-items: center;
  font-size: 18px;
  display: flex;
}

.country-card {
  max-width: 900px;
  gap: 40px;
  padding: 40px;
  position: absolute;
  top: 0;
  left: 0;
}

.country-card img {
  margin-top: auto;
  margin-bottom: auto;
}

.pagination__items-box {
  height: 50px;
  flex-shrink: 0;
  justify-content: center;
  gap: 5px;
  padding: 2px;
  display: flex;
}

.pagination {
  justify-content: space-between;
  display: flex;
}

.pagination-dot {
  width: 40px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background-color: #252121;
  border: 2px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination-dot:disabled {
  cursor: no-drop;
  background-color: #959da0;
}

.is-hidden {
  opacity: 0;
  visibility: 0;
}

.current {
  background-color: tomato;
}

.start {
  background-image: url("icon-double-arrow-left.72fdac92.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
}

.prev {
  background-image: url("icon-arrow-left.b44b8bce.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
}

.next {
  background-image: url("icon-arrow-right.d32d2065.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
}

.end {
  background-image: url("icon-double-arrow-right.593c4078.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
}

.error {
  width: 100%;
  height: 100%;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.error p {
  color: tomato;
  font-size: 30px;
}

.is-none {
  display: none;
}

